.App {
  display: grid;
  place-content: center;
  min-height: 100vh;
  position: relative;
  overflow-x: hidden;
}
h1 {
  text-align: center;
  margin: 0 0 2rem;
  color: hsl(233, 13%, 49%);
}
.pricing-component {
  display: flex;
  align-items: center;
}
.top-background {
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
}
.bottom-background {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
}
@media only screen and (max-width: 879px) {
  h1 {
    margin-top: 3rem;
  }
  .pricing-component {
    flex-direction: column;
    gap: 2rem;
  }
  .top-background {
    right: -20rem;
  }
}
